import { mapGetters, mapMutations, mapActions } from 'vuex'
import LogsTableRow from '@/components/common/LogsTableRow'
import Spinner from '@/components/filters/Spinner'
export default {
  components: { LogsTableRow, Spinner },

  computed: {
    ...mapGetters(['getUsers', 'getlogsPaginated', 'getSearch', 'getloadinglogs'])
  },

  watch: {
    getSearch: {
      handler (newVal) {
        this.loadlogsPaginated()
      }
    }
  },

  mounted () {
    this.loadUsers()
    this.loadlogsPaginated()
  },

  methods: {
    ...mapMutations(['RESET_SEARCH']),
    ...mapActions(['loadlogsPaginated', 'loadUsers']),

    loadPaginationPage (url) {
      if (this.getloadinglogs) { return }
      if (!url) { return }
      this.loadlogsPaginated(url)
    },

    reset () {
      this.RESET_SEARCH()
      this.loadlogsPaginated()
    }
  }
}
